import { useState, useEffect } from 'react';
import './App.css';
import Person from './Person';
import Individuals from './Individuals';
import Teachers from './Teachers';
import TeacherProp from './TeacherProp';

function App() {

  const [data, setData] = useState()

  const [name, setName] = useState("Mo");

  const [year, setYear] = useState(2020);


  useEffect(() => {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((res) => res.json())
      .then(data => setData(data))
  }, []);


  function Year() {
    setYear(2021);
  };

  function changeName() {
    setName("Julianna");

  };

  return (
    <div>
      <h1>Dynamic Funtions</h1>

      <p>{year}</p>
      <button onClick={Year}>Change to the correct year</button>

      <p>{name}</p>
      <button onClick={changeName}>Change Name</button>

      <h1>
        <span>Who are my teachers?</span>
      </h1>
      <dl>{Teachers.map((indivialsTeachers) => {
        return (<TeacherProp
          name={indivialsTeachers.name}
          subject={indivialsTeachers.subject}/>)
      })}</dl>

      <h1>
        <span>Import Persons in history</span>
      </h1>
      <dl>{Individuals.map((persons) => {
        return (<Person
          name={persons.name}
          age={persons.age}
          personality={persons.personality} />)
      })}</dl>

<h1>Fetching data from an API</h1>
      {data && data.map((user) => <p>{user.name}</p>)}

    </div>
  );
}

export default App;
